<template>
    <div class="all_com">
        <div class="title_com">批量发券（确认）</div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff','overflow':'hidden'}">
            <div slot="header" class="clearfix">
                <span>批量发券（{{this.sendName+'/'+this.activity_id}}）</span>
            </div>
            <div class="files_msg">
                <p class="files_msg_total">共{{accountData?accountData.length:''}}项，其中<span style="color:red;margin: 0 5px;font-size:17px">{{noCount}}</span>项有问题，请确认以下内容</p>
                <router-link :to="{path:'/coupon/send_batch',query:{activity_id:activity_id}}">
                    <el-button>重新选择</el-button>
                </router-link>
                <el-button type="primary" @click="confirmSend">提交</el-button>
            </div>
            <div class="files_table_out">
                <table class="files_table" width=100%>
                    <tr>
                        <td>用户名发券</td>
                        <!-- <td>邮箱发券</td>
                        <td>手机号发券</td> -->
                    </tr>
                    <tr v-for="(item, index) in accountData" :key="index" :class="'background'+item.type">
                        <td>{{item.key}}</td>
                    </tr>
                </table>
                <table class="files_table" width=100%>
                    <tr>
                        <td>邮箱发券</td>
                    </tr>
                    <tr v-for="(item, index) in emailData" :key="index" :class="'background'+item.type">
                        <td>{{item.key}}</td>
                    </tr>
                </table>
                <table class="files_table" width=100%>
                    <tr>
                        <td>手机号发券</td>
                    </tr>
                    <tr v-for="(item, index) in phoneNumberData" :key="index" :class="'background'+item.type">
                        <td>{{item.key}}</td>
                    </tr>
                </table>
            </div>
        </el-card>
    </div>
</template>
<script>
    import {mapState} from 'vuex'
    export default {
        data (){
            return {
                sendCouponAll: '',
                accountData: [], // files列表
                emailData:[],
                phoneNumberData:[],
                sendUserData:[],
                noCount: 0,
                activity_id:'',
                sendEndTime:'',
                sendName:''
            }
        },
        methods: {
            confirmSend(){
                let accountArr = []
                this.sendUserData.forEach((item, index) => {
                    accountArr.push(item.Account)
                })
                this.$api('/admin/coupon/new_coupon_record_add', {
                    activity_id: this.activity_id,
                    expire: this.sendEndTime,
                    accounts: accountArr.join(',')
                }).then(res => {
                    // console.log(res)
                    this.$message({
                    message: res.msg,
                    type: 'success'
                });
                this.$router.push({path: '/tool/coupon_send'})
            });
            }
        },
        mounted(){
        },
        created(){
            this.sendEndTime = JSON.parse(sessionStorage.getItem('sendEndTime'))
            this.sendName = JSON.parse(sessionStorage.getItem('sendName'))
            this.activity_id = this.$route.query.activity_id
            this.sendCouponAll = JSON.parse(sessionStorage.getItem('sendCouponAll'))
            this.sendCouponAll?this.accountData = this.sendCouponAll.data.accountData:this.accountData = []
            this.sendCouponAll?this.emailData = this.sendCouponAll.data.emailData:this.emailData = []
            this.sendCouponAll?this.phoneNumberData = this.sendCouponAll.data.phoneNumberData:this.phoneNumberData = []
            this.sendCouponAll?this.sendUserData = this.sendCouponAll.data.sendUserData:this.sendUserData = []
            // type 0无效，1有效，2不是托管订单，3日期格式错误
            console.log(this.sendCouponAll)
            let amount = 0
            if(this.accountData){
                this.accountData.forEach((item, index) => {
                    if(item.type != 1){
                        amount = amount + 1
                    }
                })
            }
            if(this.emailData){
                this.emailData.forEach((item, index) => {
                    if(item.type != 1){
                    amount = amount + 1
                    }
                })
            }
            if(this.phoneNumberData){
                this.phoneNumberData.forEach((item, index) => {
                    if(item.type != 1){
                    amount = amount + 1
                    }
                })
            }
            this.noCount = amount
        }
    }
</script>
<style lang='scss' scoped>
    @import "../../assets/css/titlecom.scss";
    .files_msg{
        height: 150px;
        border-top: 1px solid #e6e6e6;
        border-bottom: 1px solid #e6e6e6;
        padding-left: 10px
    }
    .files_msg_total{
        margin: 20px 0 50px 0;
        font-size: 16px;
    }
    .files_table_out{
        margin-top: 30px;
        margin-left: 10px
    }
    .files_table tr:first-child{
        font-size: 16px;
        font-weight: 600
    }
    .files_table td{
        border: 1px solid #e6e6e6;
        padding-left: 20px;
        height: 30px;
    }
    .background0, .background2, .background3{
        background-color: #F57A71
    }
    .files_table{
        width: 300px;
        float: left;
    }
</style>



